import db from '@/lib/db';
import { NextResponse } from 'next/server';

//REMEMBER YOU CHANGED THE FILE NAME FROM [id].ts to id.ts


export default async function GET(request: Request) {
  //request hasn't been used but can be utilized for query parameters if needed
  try{
    const userId = 1; // Example user ID
    const result = await db.query('SELECT * FROM users WHERE id = $1', [userId]);
    if(result.rows.length === 0){
      return NextResponse.json({ error: 'User not found' }, { status: 404 });
    }
    return NextResponse.json(result.rows);
  } catch (error) {
    if (error instanceof Error) {
      return NextResponse.json({ error: error.message }, { status: 500 });
    } else {
      return NextResponse.json({ error: 'An unknown error occurred' }, { status: 500 });
    }
  }
}

export async function POST(request: Request) {
  const { title, column, priority } = await request.json();
  const userId = 1; // Example user ID
  try {
    const result = await db.query(
      'INSERT INTO tasks (title, column_status, priority, user_id) VALUES ($1, $2, $3, $4) RETURNING *',
      [title, column, priority, userId]
    );
    return NextResponse.json(result.rows[0], { status: 201 });
  } catch (error) {
    if (error instanceof Error) {
      return NextResponse.json({ error: error.message }, { status: 500 });
    } else {
      return NextResponse.json({ error: 'Failed to insert task' }, { status: 500 });
    }
  }
}

export async function PUT(request: Request) {
  const { id, title, column, priority } = await request.json();
  try {
    const result = await db.query(
      'UPDATE tasks SET title = $1, column_status = $2, priority = $3 WHERE id = $4 RETURNING *',
      [title, column, priority, id]
    );
    if (result.rows.length === 0) {
      return NextResponse.json({ error: 'Task not found' }, { status: 404 });
    }
    return NextResponse.json(result.rows[0]);
  } catch (error) {
    if (error instanceof Error) {
      return NextResponse.json({ error: error.message }, { status: 500 });
    } else {
      return NextResponse.json({ error: 'Failed to update task' }, { status: 500 });
    }
  }
}

export async function DELETE(request: Request) {
  const { id } = await request.json();
  try {
    const result = await db.query('DELETE FROM tasks WHERE id = $1 RETURNING *', [id]);
    if (result.rows.length === 0) {
      return NextResponse.json({ error: 'Task not found' }, { status: 404 });
    }
    return NextResponse.json({ message: 'Task deleted successfully' });
  } catch (error) {
    if (error instanceof Error) {
      return NextResponse.json({ error: error.message }, { status: 500 });
    } else {
      return NextResponse.json({ error: 'Failed to delete task' }, { status: 500 });
    }
  }
}

